.bgoverview{
    position: relative;
    background: url('/public/image/bgmain.jpg') no-repeat center center fixed; /* ตั้งค่าพื้นหลังเป็นรูปภาพ */
    background-size: cover; /* ทำให้รูปภาพครอบคลุมพื้นที่ทั้งหมด */
    z-index: 2;
    width: 100%;
    height: 100vh;
}
.bgoverview::before {
    position: absolute;
    content: '';
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.6);     /* สีดำโปร่งแสงทับพื้นหลัง */
    z-index: -1;
  }

.logins-button{
    background-color: white; /* สีพื้นหลัง */
    color: black ; /* สีข้อความ */
    height: 50px;
    width: 150px;
    border: none; /* ไม่มีเส้นขอบ */
    border-radius: 5px; /* มุมโค้งมน */
    cursor: pointer; /* เปลี่ยนเมาส์เมื่อ hover */
    font-size: 16px; /* ขนาดตัวอักษร */
    position: absolute;
    right: 20px;
    top: 20px;
    transition: background-color 0.3s ease, transform 0.2s ease; /* การเปลี่ยนแปลงแบบนุ่มนวล */
}

