.multi-border-button {
    position: relative;
    display: inline-block;
    height: 22%;
    width: 90%;
    padding: 10px 20px;
    color: #f4f4d7; /* สีของข้อความ */
    background-color: #3b2c22; /* สีพื้นหลัง */
    font-family: 'Merriweather', serif; 
    text-align: center;
    text-transform: uppercase;
    font-size: 16px;
    letter-spacing: 1px;
    border: 2px solid #e0e0a0; /* สีของกรอบหลัก */
    box-shadow: 0 0 0 2xpx #3b2c22; /* กรอบที่สอง */
    margin: 20px;
  }
  
  .multi-border-button::before,
  .multi-border-button::after {
    content: "";
    position: absolute;
    top: -8px;
    left: -8px;
    right: -8px;
    bottom: -8px;
    border: 2px solid #e0e0a0; /* สีของกรอบ */
    pointer-events: none;
  }
  
  .multi-border-button::after {
    top: -12px;
    left: -12px;
    right: -12px;
    bottom: -12px;
    border: 2px solid #e0e0a0; /* สีของกรอบชั้นนอก */
  }
  

